.App {
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 30px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.App main {
    margin-left: 30px;
}

@media (max-width: 1000px){
    .App {
        flex-direction: column;
    }
    .App main{
        width: 100%;
        margin-left: 0;
        margin-top: 30px;
    }
    .App aside{
        width: 100%;
    }
}