li.dev-item{
    background: #FFF;
    box-shadow: 0 0 14px 0 #FFF;
    border-radius: 2px;
    padding: 20px;
}

li.dev-item header{
display: flex;
flex-direction: row;
align-items: center;
}

li.dev-item header img{
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info{
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}
.user-info span {
    margin-top: 2px;
    font-size: 13px;
    color: #999;
}

li.dev-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
} 