@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
li.dev-item{
    background: #FFF;
    box-shadow: 0 0 14px 0 #FFF;
    border-radius: 2px;
    padding: 20px;
}

li.dev-item header{
display: flex;
flex-direction: row;
align-items: center;
}

li.dev-item header img{
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info{
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}
.user-info span {
    margin-top: 2px;
    font-size: 13px;
    color: #999;
}

li.dev-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
} 
aside form {
    margin-top: 30px;
}

aside form .input-block + .input-block {
    margin-top: 20px;
}

aside form .input-group {
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

aside form .input-group .input-block {
    margin-top: 0;
}

aside form .input-block label {
    color: #acacac;
    font-size: 14px;
    font-weight: bold;
    display: block;

}

aside form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #eee;
}

aside form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #7d40e7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    transition: background 0.5s;
}

aside form button[type=submit]:hover{
    background: #6931ca;
}
.App {
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 30px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.App main {
    margin-left: 30px;
}

@media (max-width: 1000px){
    .App {
        flex-direction: column;
    }
    .App main{
        width: 100%;
        margin-left: 0;
        margin-top: 30px;
    }
    .App aside{
        width: 100%;
    }
}
main {
    flex: 1 1;
}

main ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 650px){
    main ul {
        grid-template-columns: 1fr;
    }
}


* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root {
    height: 100%;
} 

body {
    background: #e5e6f0;
    -webkit-font-smoothing: antialiased;
}

body, input, button {
 font-family: 'Roboto', sans-serif;
}
aside {
    width: 320px;
    background: #FFF;
    box-shadow: 0 0 14px 0 #FFF;
    border-radius: 2px;
    padding: 30px 20px;
}

aside strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}
